@keyframes round {
  0% {
    transform: scale(1.14);
  }

  10% {
    transform: scale(1);
  }

  16% {
    transform: scale(1.12);
  }

  40% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(1.08);
  }

  80% {
    transform: scale(1);
  }

  89% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1.07);
  }
}
