.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    box-sizing: border-box;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
